@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);@import url(http://fonts.googleapis.com/css?family=Roboto+Condensed:400,700);
.App {
    text-align: center;
    font-family: 'Roboto Condensed', sans-serif;
}
.fa {
  margin-right: 10px;
}
.Wrapper {
    width: 50%;
    border-style: solid;
    border-color: black;
    border-radius: 5px;
    min-height: 500px;
    margin: 35px auto;
}
.todoWrapper {
	float: left;
	max-height: 280px;
	width: 95%;
  overflow-y: auto;
  overflow-x: hidden;
	margin-bottom: 25px;
}
#todoScroll::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #F5F5F5;
}
#todoScroll::-webkit-scrollbar
{
	width: 12px;
	background-color: #F5F5F5;
}
#todoScroll::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #555;
}





















.todoFormContainer {
    margin: 50px auto;
}
.todoFormContainer input {
    width:75%;
    margin-right: 15px;
    border: none;
    border-top: 2px solid;
    border-bottom: 2px solid;
    padding: 5px;
}
.todoFormContainer input:focus {
    outline: none;
    border: none;
    border-top: 2px solid;
    border-bottom: 2px solid;
    padding: 5px;
}
.todoContainer {
    width:75%;
    text-align: left;
    margin-bottom: 10px;
    border-bottom: 1px solid #efefef;
    padding-bottom: 10px;
}
.todoText {
    margin-left:10px;
    font-size: 2rem;
    font-weight: 100;
}

